.navbar_dropdown_button {
  display: table;
  margin-right: auto;
  padding-left: 1rem;
  width: calc(100% - 1rem);
  height: 4rem;
  background-color: white;
  cursor: pointer;
  color: black;
  border-radius: 1rem;
  vertical-align: middle;
}

.navbar_dropdown_button:hover {
  background-color: whitesmoke;
}

.navbar_dropdown_button_icon {
  display: table-cell;
  vertical-align: middle;
  width: 4rem;
}

.navbar_dropdown_button_title {
  display: table-cell;
  vertical-align: middle;
  color: black;
  font-size: 1.2rem;
}

.AddCollectionIcon {
  fill: gray;
  height: 2rem;
  width: 2rem;
  padding: 0.5rem;
  background-color: whitesmoke;
  border-radius: 2rem;
}

.SubjectIcon {
  fill: gray;
  height: 2rem;
  width: 2rem;
  padding: 0.5rem;
  background-color: whitesmoke;
  border-radius: 2rem;
}

.StarIcon {
  fill: #fcba03;
  height: 2rem;
  width: 2rem;
  padding: 0.5rem;
  background-color: whitesmoke;
  border-radius: 2rem;
}

.SettingsIcon {
  fill: gray;
  height: 2rem;
  width: 2rem;
  padding: 0.5rem;
  background-color: whitesmoke;
  border-radius: 2rem;
}

.LogOutIcon {
  fill: gray;
  height: 2rem;
  width: 2rem;
  padding: 0.5rem;
  background-color: whitesmoke;
  border-radius: 2rem;
}

button::-moz-focus-inner {
  border: 0;
}

.navbar_action_buttons {
  position: fixed;
  height: 4rem;
  width: 12.5rem;
  /* top: 0;
  right: 0; */
}
